




























































































































































































import { Component, Vue } from 'vue-property-decorator';
import jQuery from 'jQuery';

const fadein = () => {
  $('.menu-item').css({
    opacity: 1,
    transform: 'translate(0px, 0px)',
  });
};

@Component
export default class SubMenu extends Vue {
  private mounted() {
    fadein();
  }
}
